import { useSelector } from 'react-redux';
import Avatar from '../Avatar/Avatar';
import './Header.css';

function Header() {
	const user = useSelector(state => state.user.user);

	return (
		<header
			className="header"
		>
			Spotify Zero Mix
			{user?.images && <Avatar />}
		</header>
	);
}

export default Header;
