import { useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import { useLocation } from 'react-router';
import createRipple from '../../utils/ripple';
import './MobileNav.css';

function MobileNav({ links }) {
	const location = useLocation();

	useEffect(() => {
		const activeLink = document.querySelector('.mobilenav__link.active');
		const links = [...document.querySelectorAll('.mobilenav__link')].filter(
			link => link !== activeLink
		);

		const icon = activeLink.querySelector('span');
		icon.classList.add('material-icons');
		icon.classList.remove('material-icons-outlined');

		links.forEach(link => {
			const icon = link.querySelector('span');
			icon.classList.remove('material-icons');
			icon.classList.add('material-icons-outlined');
		});
	}, [location.pathname]);

	return (
		<ul className="mobilenav">
			{links.map((link, i) => (
				<NavLink
					to={link.to}
					key={i}
					className={({ isActive }) =>
						'mobilenav__link' + (isActive ? ' active' : '')
					}
					onClick={e => createRipple(e)}
				>
					<span className="material-icons-outlined">{link.span}</span>
					<label className="mobilenav__label">{link.name}</label>
				</NavLink>
			))}
		</ul>
	);
}

export default MobileNav;
