import { NavLink } from 'react-router-dom';
import createRipple from '../../utils/ripple';
import './Navbar.css';

function Navbar({ links }) {
	// function hover(e) {
	// 	const link = e.target;

	// 	if (!link.classList.contains('navbar__link_active')) {
	// 		link.style.backgroundColor = colorTheme.primary[90];
	// 		link.style.cursor = 'pointer';
	// 	}
	// }

	// function leave(e) {
	// 	const link = e.target;

	// 	if (link.classList.contains('navbar__link_active')) {
	// 		link.style.backgroundColor = colorTheme.primary[80];
	// 	} else {
	// 		link.style.backgroundColor = 'transparent';
	// 	}
	// }

	return (
		<ul
			className="navbar"
			onClick={e => createRipple(e, 'red')}
		>
			{links.map((link, i) => (
				<li className="navbar__item" key={i}>
					<NavLink
						to={link.to}
						className={({ isActive }) =>
							'navbar__link' + (isActive ? ' navbar__link_active' : '')
						}
						style={({ isActive }) => ({
							backgroundColor: isActive
								? 'red'
								: 'transparent',
						})}
						// onMouseEnter={e => hover(e)}
						// onMouseLeave={e => leave(e)}
					>
						{link.name}
					</NavLink>
				</li>
			))}
		</ul>
	);
}

export default Navbar;
