import auth from '../../auth/auth';
import Button from '../Button/Button';
import './Login.css';

function Login() {
	return (
		<section className="login">
			<h2 className="title">Войти в Spotify</h2>
			<Button
				onClick={auth.login}
				className="login__button button"
				position='absolute'
			>
				Войти
			</Button>
		</section>
	);
}

export default Login;
